  .Edit-image {
    display: none;
    position: absolute;
    border-radius: 10em;
    padding: 2px;
    text-decoration: none;
    background: #555;
    border: 3px solid #fff;
    color: #fff;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5), inset 0 2px 4px rgba(0, 0, 0, 0.3);
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
    -webkit-transition: background 0.5s;
    transition: background 0.5s;
    text-decoration: none;
    width: 30px;
    height: 30px;
  }
  
  .Edit-image:hover {
    background: green;
    cursor: pointer;
  }
  
  .Delete-image {
    display: none;
    position: absolute;
    /* top: 180px;
    left: 115px; */
    border-radius: 10em;
    padding: 2px;
    text-decoration: none;
    /* font: 700 21px/20px sans-serif; */
    background: #555;
    border: 3px solid #fff;
    color: #fff;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5), inset 0 2px 4px rgba(0, 0, 0, 0.3);
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
    -webkit-transition: background 0.5s;
