    text-decoration: none;
    width: 30px;
    height: 30px;
  }
  
  .Delete-image:hover {
    background: red;
    cursor: pointer;
  }
  .Document_Upload {
    display: flex;
    gap: 40px;
    border: 1px solid gray;
    padding: 11px;
    border-radius: 5px;
    box-shadow: 1px 1px 2px black;
    height: 220px;
  }
  
  .UrlData {
    opacity: 1;
  }
  
  .UrlData:hover {
    opacity: 0.3;
    cursor: zoom-in;
  }
  
  .UrlData:before {
    content: " ";
    display: block;
    position: absolute;
    height: 150px;
    /* background-repeat: no-repeat; */
    width: 150px;
    background-image: url(../utils/UiImages/defultImage.png);
